<template>
  <view>
    <!-- È°∂ÈÉ®Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
    <view :style="{height: statusBarHeight + 'px'}"></view>
    <view class="custom-navbar">
      <view class="back-btn" @click="goBack">
        <image src="/static/3.png" class="back-icon"></image>
      </view>
      <view class="navbar-title-wrap">
        <view class="navbar-title">ÊàëÁöÑÁè≠Á∫ß</view>
      </view>
      <view class="navbar-placeholder"></view>
    </view>

    <!-- È°∂ÈÉ®ÊéßÂà∂Ê†è -->
    <view class="control-bar">
      <!-- Á±ªÂûãÂàáÊç¢tab -->
      <view class="type-tabs">
        <view
          v-for="type in typeList"
          :key="type"
          :class="['type-tab', selectedType === type ? 'active' : '']"
          @click="selectType(type)"
        >{{ type }}</view>
      </view>
      
      <!-- Â≠¶Âπ¥ÈÄâÊã©Âô® -->
      <view class="year-selector" @click="showYearPicker">
        <text class="year-text">{{ selectedYear }}</text>
        <text class="dropdown-arrow">‚ñº</text>
      </view>
    </view>

    <view class="main-container">
      <!-- Â∑¶‰æßÔºöÁè≠ËØæ‰∏∫Áè≠ËØæÂêçÔºåÂÖ∂ÂÆÉ‰∏∫Âπ¥Á∫ß -->
      <view v-if="leftList.length > 0" class="sidebar">
        <view
          v-for="(item, idx) in leftList"
          :key="item"
          :class="['side-item', selectedLeft === item ? 'active' : '']"
          @click="selectLeft(item)"
        >
          {{ item }}
          <template v-if="classStatusMap[item]">
            <text v-if="classStatusMap[item].isOver" style="color:#f56c6c;font-size:24rpx;">ÔºàÂ∑≤ÁªìËØæÔºâ</text>
          </template>
        </view>
      </view>
      <!-- Âè≥‰æßÂÜÖÂÆπÂå∫Âüü -->
      <view class="content-panel">
        <view v-if="filteredStudents.length > 0" class="content-list">
          <!-- Áè≠Á∫ßÂç°Áâá -->
          <view 
            v-for="student in classItems"
            :key="student._id"
            class="class-card" 
            @click="showStudentDetail(student)"
          >
            <view class="class-header">
              <view class="class-icon">üìö</view>
              <view class="class-info">
                <text class="class-name">{{ student.name }}</text>
                <text class="class-meta">{{ student.grade || 'Âπ¥Á∫ß' }} ¬∑ {{ student.subject || 'ÁªºÂêà' }}</text>
              </view>
              <view class="class-stats">
                <text class="student-count">{{ student.studentCount }}</text>
                <text class="count-label">‰∫∫</text>
              </view>
            </view>
            <view class="class-footer">
              <text class="class-year">{{ student.classYear }}</text>
              <view class="class-arrow">‚Ä∫</view>
            </view>
          </view>
          
          <!-- Â≠¶ÁîüÂç°Áâá -->
          <view 
            v-for="student in studentItems"
            :key="student._id"
            class="student-card" 
            @click="showStudentDetail(student)"
          >
            <view class="student-avatar">
              <text class="avatar-text">{{ student.name.charAt(0) }}</text>
            </view>
            <view class="student-content">
              <view class="student-header">
                <text class="student-name">{{ student.name }}</text>
                <text class="student-id">{{ student.studentId }}</text>
              </view>
              <view class="student-meta">
                <text class="student-grade">{{ student.grade }}</text>
                <text class="student-class">{{ student.className }}</text>
              </view>
              <view class="score-section" v-if="student.latestScore">
                <view class="score-item">
                  <text class="score-label">{{ student.latestScore.subject }}</text>
                  <view class="score-badge" :class="student.latestScore.scoreLevel">
                    <text class="score-value">{{ student.latestScore.score }}</text>
                  </view>
                </view>
                <text class="score-date">{{ student.latestScore.formattedDate }}</text>
              </view>
              <view class="no-score" v-else>
                <text class="no-score-text">ÊöÇÊó†ÊàêÁª©</text>
              </view>
            </view>
            <view class="student-arrow">‚Ä∫</view>
          </view>
        </view>
        <view v-else class="empty-state">
          <view class="empty-icon">üìù</view>
          <text class="empty-title">{{ selectedType === 'Áè≠Á∫ß' ? 'ÊöÇÊó†Áè≠Á∫ß' : 'ÊöÇÊó†Â≠¶Áîü' }}</text>
          <text class="empty-desc">{{ selectedType === 'Áè≠Á∫ß' ? 'ÊÇ®ËøòÊ≤°ÊúâË¥üË¥£ÁöÑÁè≠Á∫ß' : 'ËØ∑ÂÖàÈÄâÊã©Áè≠Á∫ßÊü•ÁúãÂ≠¶Áîü' }}</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      statusBarHeight: 0,
      students: [],
      leftList: [], // Áè≠ËØæÂêçÊàñÂπ¥Á∫ß
      selectedLeft: '',
      userInfo: null,
      classStatusMap: {}, // ËÆ∞ÂΩïÁè≠ËØæÁä∂ÊÄÅÂíå‰∫∫Êï∞
      myClasses: [], // ‰ªéÁè≠Á∫ßÁÆ°ÁêÜÁªëÂÆöÁöÑÁè≠Á∫ß
      allHistoryClasses: [], // ÊâÄÊúâÂéÜÂè≤Áè≠Á∫ßÊï∞ÊçÆ
      showMyClasses: false, // ÊòØÂê¶ÊòæÁ§∫Áè≠Á∫ßÁÆ°ÁêÜÁöÑÁè≠Á∫ß
      teacherSubject: '', // ËÄÅÂ∏àÁöÑÁßëÁõÆ
      typeList: ['Áè≠Á∫ß', 'Â≠¶Áîü'],
      selectedType: 'Áè≠Á∫ß',
      selectedYear: 'ÂΩìÂâçÂ≠¶Âπ¥', // ÈÄâ‰∏≠ÁöÑÂ≠¶Âπ¥
      availableYears: ['ÂΩìÂâçÂ≠¶Âπ¥'], // ÂèØÈÄâÁöÑÂ≠¶Âπ¥ÂàóË°®
      showYearPickerModal: false // ÊòØÂê¶ÊòæÁ§∫Â≠¶Âπ¥ÈÄâÊã©Âô®
    }
  },
  computed: {
    filteredStudents() {
      // Ê†πÊçÆÈÄâ‰∏≠ÁöÑÂ≠¶Âπ¥ËøáÊª§Áè≠Á∫ß
      let classesToShow = this.myClasses;
      if (this.selectedYear !== 'ÂΩìÂâçÂ≠¶Âπ¥') {
        classesToShow = this.allHistoryClasses.filter(c => 
          this.getClassYear(c.className) === this.selectedYear
        );
      }
      
      if (this.selectedType === 'Áè≠Á∫ß') {
        // Áè≠Á∫ßtabÔºöÊòæÁ§∫ÊâÄÊúâÁè≠Á∫ßÔºå‰∏çÈúÄË¶ÅÂ∑¶‰æßÁ≠õÈÄâ
        return classesToShow.map(myClass => ({
          _id: myClass._id,
          name: myClass.className,
          grade: myClass.grade,
          subject: myClass.subject,
          studentCount: myClass.classStudents ? myClass.classStudents.length : myClass.studentCount || 0,
          isClass: true // Ê†áËÆ∞ËøôÊòØÁè≠Á∫ßÊï∞ÊçÆ
        }));
      } else {
        // Â≠¶ÁîütabÔºöÊòæÁ§∫ÈÄâ‰∏≠Áè≠Á∫ßÁöÑÂ≠¶Áîü
        const selectedClass = classesToShow.find(c => c.className === this.selectedLeft);
        if (selectedClass && selectedClass.classStudents) {
          return selectedClass.classStudents.map(student => {
            return {
              ...student,
              className: selectedClass.className,
              grade: student.grade,
              latestScore: student.latestScore, // ÊòæÁ§∫ÊúÄÊñ∞ÊàêÁª©
              isClass: false // Ê†áËÆ∞ËøôÊòØÂ≠¶ÁîüÊï∞ÊçÆ
            };
          });
        }
        return [];
      }
    },
    
    classItems() {
      return this.filteredStudents.filter(item => item.isClass).map(item => ({
        ...item,
        _id: 'class_' + item._id, // Ê∑ªÂä†ÂâçÁºÄÈÅøÂÖç‰∏éÂ≠¶ÁîüIDÂÜ≤Á™Å
        classYear: this.getClassYear(item.name) // Ê∑ªÂä†Â≠¶Âπ¥‰ø°ÊÅØ
      }));
    },
    
    studentItems() {
      return this.filteredStudents.filter(item => !item.isClass).map(item => ({
        ...item,
        _id: 'student_' + item._id // Ê∑ªÂä†ÂâçÁºÄÈÅøÂÖç‰∏éÁè≠Á∫ßIDÂÜ≤Á™Å
      }));
    }
  },
  onLoad() {
    this.statusBarHeight = uni.getSystemInfoSync().statusBarHeight;
    this.userInfo = uni.getStorageSync('userInfo');
    this.teacherSubject = this.userInfo.subject || this.userInfo.teacherSubject || '';
    this.loadMyClasses();
  },
  methods: {
    // Ê†πÊçÆÂπ¥Á∫ßÂíåÂàÜÊï∞Á°ÆÂÆöÈ¢úËâ≤Á≠âÁ∫ßÔºà‰∏éËøõÊ≠•ÂàÜÊûêÂäüËÉΩ‰øùÊåÅ‰∏ÄËá¥Ôºâ
    getScoreColor(grade, score) {
      if (!score || isNaN(score)) return 'score-default';
      
      // ÊèêÂèñÂπ¥Á∫ßÊï∞Â≠ó
      const gradeMatch = grade.match(/(\d+)/);
      if (!gradeMatch) return 'score-default';
      
      const gradeNum = parseInt(gradeMatch[1]);
      
      // 1-6Âπ¥Á∫ßÁöÑËØÑÂàÜÊ†áÂáÜÔºà‰∏éËøõÊ≠•ÂàÜÊûêÂäüËÉΩ‰∏ÄËá¥Ôºâ
      if (gradeNum >= 1 && gradeNum <= 6) {
        if (score >= 95 && score <= 100) {
          return 'score-green'; // ÁªøËâ≤ - È´òÂàÜÊÆµ
        } else if (score >= 90 && score <= 94) {
          return 'score-green'; // ÁªøËâ≤ - ‰∏≠‰∏äÂàÜÊÆµ
        } else if (score >= 60 && score <= 89) {
          return 'score-yellow'; // ÈªÑËâ≤ - ‰∏≠Á≠âÂàÜÊÆµ
        } else {
          return 'score-red'; // Á∫¢Ëâ≤ - ‰ΩéÂàÜÊÆµ
        }
      }
      // 7-9Âπ¥Á∫ßÁöÑËØÑÂàÜÊ†áÂáÜÔºà‰∏éËøõÊ≠•ÂàÜÊûêÂäüËÉΩ‰∏ÄËá¥Ôºâ
      else if (gradeNum >= 7 && gradeNum <= 9) {
        if (score >= 100 && score <= 120) {
          return 'score-green'; // ÁªøËâ≤ - È´òÂàÜÊÆµ
        } else if (score >= 90 && score <= 99) {
          return 'score-green'; // ÁªøËâ≤ - ‰∏≠‰∏äÂàÜÊÆµ
        } else if (score >= 72 && score <= 89) {
          return 'score-yellow'; // ÈªÑËâ≤ - ‰∏≠Á≠âÂàÜÊÆµ
        } else {
          return 'score-red'; // Á∫¢Ëâ≤ - ‰ΩéÂàÜÊÆµ
        }
      }
      
      return 'score-default';
    },
    goBack() {
      if (getCurrentPages().length > 1) {
        uni.navigateBack();
      } else {
        uni.switchTab({ url: '/pages/home/home' });
      }
    },
    selectType(type) {
      this.selectedType = type;
      this.updateLeftList(); // ÂàáÊç¢tabÂêéÊõ¥Êñ∞Â∑¶‰æßÂàóË°®
    },
    selectLeft(item) {
      this.selectedLeft = item;
      // Â¶ÇÊûúÂΩìÂâçÊòØÂ≠¶ÁîütabÔºåÈúÄË¶ÅÈáçÊñ∞ËÆ°ÁÆóÂ≠¶ÁîüÂàóË°®
      if (this.selectedType === 'Â≠¶Áîü') {
        // Âº∫Âà∂Êõ¥Êñ∞ËÆ°ÁÆóÂ±ûÊÄß
        this.$forceUpdate();
      }
    },
    async loadMyStudents() {
      if (!this.userInfo) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
        return;
      }
      try {
        uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
        const result = await uniCloud.callFunction({
          name: 'student-hours',
          data: {
            action: 'getMyStudents',
            teacherId: this.userInfo._id || this.userInfo.uid
          }
        });
        if (result.result.success) {
          this.students = result.result.data || [];
          this.updateLeftList();
        } else {
          uni.showToast({ title: result.result.message || 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' });
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊàëÁöÑÂ≠¶ÁîüÂ§±Ë¥•:', error);
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        uni.hideLoading();
      }
    },
    async loadMyClasses() {
      if (!this.userInfo) {
        return;
      }
      try {
        console.log('Áî®Êà∑‰ø°ÊÅØ:', this.userInfo);
        console.log('Áî®Êà∑‰ø°ÊÅØÊâÄÊúâÂ≠óÊÆµ:', Object.keys(this.userInfo));
        const teacherId = this.userInfo._id || this.userInfo.uid || this.userInfo.id || this.userInfo.teacherId;
        console.log('‰ΩøÁî®ÁöÑteacherId:', teacherId);
        
        // Âä†ËΩΩÂΩìÂâçÂ≠¶Âπ¥ÁöÑÁè≠Á∫ß
        const result = await uniCloud.callFunction({
          name: 'class-management',
          data: {
            action: 'getMyClasses',
            teacherId: teacherId
          }
        });
        
        if (result.result.code === 0) {
          this.myClasses = result.result.data || [];
          console.log('Âä†ËΩΩÁöÑÁè≠Á∫ßÊï∞ÊçÆ:', this.myClasses);
          console.log('Áè≠Á∫ßÊï∞Èáè:', this.myClasses.length);
          
          // Âä†ËΩΩÂéÜÂè≤Áè≠Á∫ßÊï∞ÊçÆ
          await this.loadHistoryClasses(teacherId);
          
          // Êõ¥Êñ∞ÂèØÁî®Â≠¶Âπ¥ÂàóË°®
          this.updateAvailableYears();
          
          this.updateLeftList();
          
          // Ë∞ÉËØïËÆ°ÁÆóÂ±ûÊÄß
          console.log('filteredStudents:', this.filteredStudents);
          console.log('classItems:', this.classItems);
          if (this.classItems.length > 0) {
            console.log('Á¨¨‰∏Ä‰∏™Áè≠Á∫ßÈ°πÁõÆ:', this.classItems[0]);
          }
        } else {
          console.log('Âä†ËΩΩÁè≠Á∫ßÂ§±Ë¥•:', result.result.message);
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊàëÁöÑÁè≠Á∫ßÂ§±Ë¥•:', error);
      }
    },
    
    async loadHistoryClasses(teacherId) {
      try {
        const result = await uniCloud.callFunction({
          name: 'class-management',
          data: {
            action: 'getHistoryClasses',
            teacherId: teacherId
          }
        });
        
        if (result.result.code === 0) {
          this.allHistoryClasses = result.result.data || [];
          console.log('Âä†ËΩΩÁöÑÂéÜÂè≤Áè≠Á∫ßÊï∞ÊçÆ:', this.allHistoryClasses);
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂéÜÂè≤Áè≠Á∫ßÂ§±Ë¥•:', error);
      }
    },
    
    updateAvailableYears() {
      const years = new Set(['ÂΩìÂâçÂ≠¶Âπ¥']);
      
      // ‰ªéÂΩìÂâçÁè≠Á∫ß‰∏≠ÊèêÂèñÂ≠¶Âπ¥
      this.myClasses.forEach(cls => {
        const year = this.getClassYear(cls.className);
        if (year) years.add(year);
      });
      
      // ‰ªéÂéÜÂè≤Áè≠Á∫ß‰∏≠ÊèêÂèñÂ≠¶Âπ¥
      this.allHistoryClasses.forEach(cls => {
        const year = this.getClassYear(cls.className);
        if (year) years.add(year);
      });
      
      this.availableYears = Array.from(years).sort((a, b) => {
        if (a === 'ÂΩìÂâçÂ≠¶Âπ¥') return -1;
        if (b === 'ÂΩìÂâçÂ≠¶Âπ¥') return 1;
        return b.localeCompare(a); // ÈôçÂ∫èÊéíÂàó
      });
    },
    
    
    showYearPicker() {
      uni.showActionSheet({
        itemList: this.availableYears,
        success: (res) => {
          this.selectedYear = this.availableYears[res.tapIndex];
          this.updateLeftList();
        }
      });
    },
    
    getClassYear(className) {
      // ‰ªéÁè≠Á∫ßÂêçÁß∞‰∏≠ÊèêÂèñÂ≠¶Âπ¥ÔºåÂ¶Ç "25Áßã‰∏âÂπ¥Á∫ßÊï∞Â≠¶ÈæôÁè≠" -> "25Áßã"
      const match = className.match(/^(\d{2}[Êò•Áßã])/);
      return match ? match[1] : '';
    },
    
    updateLeftList() {
      this.leftList = [];
      this.classStatusMap = {};
      this.selectedLeft = ''; // ÈªòËÆ§Ê∏ÖÁ©∫ÈÄâ‰∏≠È°π

      if (this.selectedType === 'Áè≠Á∫ß') {
        // Áè≠Á∫ßtabÔºöÂ∑¶‰æßÂàóË°®‰∏∫Á©∫Ôºå‰∏çÈúÄË¶ÅÊòæÁ§∫ÂÜÖÂÆπ
        console.log('Áè≠Á∫ßtabÔºöÂ∑¶‰æßÂàóË°®‰∏∫Á©∫');
        // Âè≥‰æßÂ∞ÜÊòæÁ§∫ÊâÄÊúâÁè≠Á∫ßÔºå‰∏çÈúÄË¶ÅÂ∑¶‰æßÁ≠õÈÄâ
      } else if (this.selectedType === 'Â≠¶Áîü') {
        // Â≠¶ÁîütabÔºöÂ∑¶‰æßÊòæÁ§∫Áè≠Á∫ßÂàóË°®
        const classNames = this.myClasses.map(c => c.className);
        this.leftList = [...new Set(classNames)]; // Á°Æ‰øùÁè≠Á∫ßÂêçÁß∞ÂîØ‰∏Ä

        console.log('Â≠¶ÁîütabÔºöÊõ¥Êñ∞Â∑¶‰æßÂàóË°®:', this.leftList);

        // ÁªüËÆ°ÊØè‰∏™Áè≠Á∫ßÁöÑÂ≠¶ÁîüÊï∞Èáè
        this.myClasses.forEach(myClass => {
          this.classStatusMap[myClass.className] = {
            total: myClass.classStudents.length,
            inRead: myClass.classStudents.length,
            isOver: false,
            isMyClass: true
          };
        });

        // ÈªòËÆ§ÈÄâ‰∏≠Á¨¨‰∏Ä‰∏™Áè≠Á∫ß
        if (this.leftList.length > 0) {
          this.selectedLeft = this.leftList[0];
        }
        console.log('Â≠¶ÁîütabÔºöÈÄâ‰∏≠ÁöÑÁè≠Á∫ß:', this.selectedLeft);
      }
    },
    showStudentDetail(item) {
      if (item.isClass) {
        // Â¶ÇÊûúÊòØÁè≠Á∫ßÔºåÂàáÊç¢Âà∞Â≠¶ÁîütabÂπ∂ÈÄâÊã©ËØ•Áè≠Á∫ß
        console.log('ÁÇπÂáªÁè≠Á∫ßÔºåÂàáÊç¢Âà∞Â≠¶ÁîüËßÜÂõæ:', item.name);
        this.selectedType = 'Â≠¶Áîü';
        this.selectedLeft = item.name;
        this.updateLeftList();
      } else {
        // Â¶ÇÊûúÊòØÂ≠¶ÁîüÔºåÊòæÁ§∫Â≠¶ÁîüËØ¶ÊÉÖ
        const content = `ÂßìÂêçÔºö${item.name}\nÂπ¥Á∫ßÔºö${item.grade}\nÂ≠¶Âè∑Ôºö${item.studentId}${item.latestScore ? `\nÊúÄËøëÊàêÁª©Ôºö${item.latestScore.score}ÂàÜÔºà${item.latestScore.subject}Ôºâ\nËÄÉËØïÊó•ÊúüÔºö${item.latestScore.examDate}` : ''}`;
        uni.showModal({
          title: 'Â≠¶ÁîüËØ¶ÊÉÖ',
          content: content,
          showCancel: false,
          confirmText: 'Á°ÆÂÆö'
        });
      }
    }
  }
}
</script>

